<template>
    <div class="container">
      <stack
              :column-min-width="300"
              :gutter-width="15"
              :gutter-height="15"
              monitor-images-loaded
      >
        <stack-item
                v-for="(image, i) in info.data"
		:key="i"
                style="transition: transform 300ms"
 >          <img :src=image />
        </stack-item>
      </stack>
    </div>
</template>

<script>
import axios from "axios";
import { Stack, StackItem } from "vue-stack-grid";
export default {
  name: 'App',
  components: {
	Stack,
	StackItem

  },
methods: {
    OnEnd: function() {
      console.log(this.info);
}

},
  data () {
    return {
      info: []
    }
  },
  mounted () {
    axios
      .get('https://mpengs.nyc/get_event_pics')
      .then(response => (
	this.info = response.data
      ))
 
 }
}
</script>

